import { NotificationService } from '../notificationService';
import { Pet, IPet } from '../../models/Pet';
import { User, IUser } from '../../models/User';
import { Notification, NotificationType, NotificationPriority } from '../../models/Notification';
import { logger } from '../../utils/logger';

export interface ReminderConfig {
  enabled: boolean;
  intervals: number[]; // days after pet lost/found
  maxReminders: number;
}

export class ReminderService {
  private static config: ReminderConfig = {
    enabled: true,
    intervals: [1, 3, 7, 14, 30], // 1Â§©, 3Â§©, 1ÈÄ±, 2ÈÄ±, 1ÂÄãÊúà
    maxReminders: 5
  };
  
  private static interval: NodeJS.Timeout | null = null;

  /**
   * ÂàùÂßãÂåñÊèêÈÜíÊúçÂãô
   */
  static initialize(config?: Partial<ReminderConfig>): void {
    if (config) {
      this.config = { ...this.config, ...config };
    }

    this.start();
    logger.info('ÊèêÈÜíÊúçÂãôÂ∑≤ÂàùÂßãÂåñ', { config: this.config });
  }

  /**
   * ÂïüÂãïÊèêÈÜí‰ªªÂãô
   */
  static start(): void {
    if (!this.config.enabled || this.interval) return;

    // ÊØèÂ∞èÊôÇÊ™¢Êü•‰∏ÄÊ¨°ÊèêÈÜí
    this.interval = setInterval(async () => {
      try {
        await this.processReminders();
      } catch (error) {
        logger.error('ÂÆöÊúüÊèêÈÜí‰ªªÂãôÂ§±Êïó', { error });
      }
    }, 60 * 60 * 1000); // 1Â∞èÊôÇ

    logger.info('ÂÆöÊúüÊèêÈÜí‰ªªÂãôÂ∑≤ÂïüÂãï');
  }

  /**
   * ÂÅúÊ≠¢ÊèêÈÜí‰ªªÂãô
   */
  static stop(): void {
    if (this.interval) {
      clearInterval(this.interval);
      this.interval = null;
      logger.info('ÂÆöÊúüÊèêÈÜí‰ªªÂãôÂ∑≤ÂÅúÊ≠¢');
    }
  }

  /**
   * ËôïÁêÜÂÆöÊúüÊèêÈÜí
   */
  private static async processReminders(): Promise<void> {
    try {
      const now = new Date();
      
      for (const days of this.config.intervals) {
        const targetDate = new Date(now.getTime() - days * 24 * 60 * 60 * 1000);
        const startOfDay = new Date(targetDate.getFullYear(), targetDate.getMonth(), targetDate.getDate());
        const endOfDay = new Date(startOfDay.getTime() + 24 * 60 * 60 * 1000);

        // Êü•ÊâæÂú®ÁõÆÊ®ôÊó•ÊúüÂâµÂª∫‰∏î‰ªçÊú™ÊâæÂà∞ÁöÑÂØµÁâ©
        const pets = await Pet.find({
          status: { $in: ['lost', 'found'] },
          createdAt: {
            $gte: startOfDay,
            $lt: endOfDay
          }
        }).populate('owner');

        for (const pet of pets) {
          try {
            // Ê™¢Êü•ÊòØÂê¶Â∑≤ÁôºÈÄÅÈÅéÊ≠§ÊèêÈÜí
            const existingReminder = await Notification.findOne({
              userId: pet.userId,
              type: 'REMINDER',
              'data.petId': pet._id.toString(),
              'data.reminderDay': days
            });

            if (!existingReminder) {
              await this.sendReminderNotification(pet, days);
            }
          } catch (error) {
            logger.error('ÁôºÈÄÅÊèêÈÜíÈÄöÁü•Â§±Êïó', { 
              petId: pet._id, 
              days, 
              error 
            });
          }
        }
      }
    } catch (error) {
      logger.error('ËôïÁêÜÂÆöÊúüÊèêÈÜíÂ§±Êïó', { error });
    }
  }

  /**
   * ÁôºÈÄÅÊèêÈÜíÈÄöÁü•
   */
  private static async sendReminderNotification(
    pet: IPet,
    days: number
  ): Promise<void> {
    const owner = await User.findById(pet.userId) as IUser;
    const isLost = pet.status === 'lost';
    
    let title: string;
    let message: string;
    
    if (days === 1) {
      title = isLost ? 'üîç ÊåÅÁ∫åÂ∞ãÊâæ‰∏≠' : 'üì¢ ÊåÅÁ∫åÂçîÂ∞ã‰∏≠';
      message = `ÊÇ®ÁöÑ${isLost ? 'Â§±Ëπ§' : 'ÊãæÁç≤'}ÂØµÁâ© ${pet.name} Â∑≤Á∂ì${days}Â§©‰∫ÜÔºåÂª∫Ë≠∞ÊÇ®Êõ¥Êñ∞Ë≥áË®äÊàñÊì¥Â§ßÊêúÂ∞ãÁØÑÂúç„ÄÇ`;
    } else if (days <= 7) {
      title = isLost ? 'üí™ ‰∏çË¶ÅÊîæÊ£Ñ' : 'ü§ù ÁπºÁ∫åÂçîÂä©';
      message = `${pet.name} Â∑≤Á∂ì${isLost ? 'Â§±Ëπ§' : 'Á≠âÂæÖË™çÈ†ò'}${days}Â§©‰∫ÜÔºåÊàëÂÄëÂª∫Ë≠∞ÊÇ®ÂàÜ‰∫´Âà∞Êõ¥Â§öÁ§æÁæ§Âπ≥Âè∞„ÄÇ`;
    } else {
      title = isLost ? 'üåü ‰øùÊåÅÂ∏åÊúõ' : '‚ù§Ô∏è ÊÑüË¨ùÊÇ®ÁöÑÊÑõÂøÉ';
      message = `${pet.name} Â∑≤Á∂ì${days}Â§©‰∫ÜÔºåÈõñÁÑ∂ÊôÇÈñìËºÉÈï∑Ôºå‰ΩÜ‰ªçÊúâÂæàÂ§öÊàêÂäüÊ°à‰æã„ÄÇÂª∫Ë≠∞ËÅØÁπ´Áï∂Âú∞ÂãïÁâ©Êî∂ÂÆπÊâÄ„ÄÇ`;
    }

    await NotificationService.sendNotification({
      userId: owner._id.toString(),
      type: 'REMINDER' as NotificationType,
      title,
      message,
      data: {
        petId: pet._id.toString(),
        reminderDay: days,
        suggestions: this.getReminderSuggestions(pet, days)
      },
      priority: days <= 3 ? NotificationPriority.HIGH : NotificationPriority.NORMAL
    });
  }

  /**
   * Áç≤ÂèñÊèêÈÜíÂª∫Ë≠∞
   */
  private static getReminderSuggestions(pet: IPet, days: number): string[] {
    const suggestions: string[] = [];
    const isLost = pet.status === 'lost';

    if (days === 1) {
      suggestions.push(
        isLost ? 'Âú®ÈôÑËøëÂºµË≤ºÂ∞ãÂØµÂïü‰∫ã' : 'ËÅØÁπ´Áï∂Âú∞ÂãïÁâ©Êî∂ÂÆπÊâÄ',
        'ÂàÜ‰∫´Âà∞Á§æÁæ§Â™íÈ´î',
        'ÈÄöÁü•ÈÑ∞Â±ÖÂíåÊúãÂèã'
      );
    } else if (days <= 7) {
      suggestions.push(
        'Êì¥Â§ßÊêúÂ∞ãÁØÑÂúç',
        'ËÅØÁπ´Áç∏ÈÜ´Ë®∫ÊâÄ',
        'Âú®ÂØµÁâ©Áõ∏ÈóúË´ñÂ£áÁôºÂ∏É',
        'ËÄÉÊÖÆÊèê‰æõÁçéÂãµ'
      );
    } else {
      suggestions.push(
        'ËÅØÁπ´ÂãïÁâ©Êî∂ÂÆπÊâÄÂíåÊïëÊè¥ÁµÑÁπî',
        'Âú®Êõ¥Â§öÂπ≥Âè∞ÁôºÂ∏ÉË≥áË®ä',
        'ËÄÉÊÖÆÂ∞àÊ•≠Â∞ãÂØµÊúçÂãô',
        '‰øùÊåÅË≥áË®äÊõ¥Êñ∞'
      );
    }

    return suggestions;
  }

  /**
   * ÊâãÂãïÁôºÈÄÅÊèêÈÜí
   */
  static async sendManualReminder(
    userId: string,
    petId: string,
    customMessage?: string
  ): Promise<void> {
    const pet = await Pet.findOne({ _id: petId, owner: userId });
    if (!pet) {
      throw new Error('ÂØµÁâ©‰∏çÂ≠òÂú®ÊàñÁÑ°Ê¨äÈôê');
    }

    const owner = await User.findById(userId) as IUser;
    const daysSinceCreated = Math.floor(
      (Date.now() - pet.createdAt.getTime()) / (24 * 60 * 60 * 1000)
    );

    await NotificationService.sendNotification({
      userId: owner._id.toString(),
      type: 'REMINDER' as NotificationType,
      title: 'üìù ÊâãÂãïÊèêÈÜí',
      message: customMessage || `ÈóúÊñºÊÇ®ÁöÑ${pet.status === 'lost' ? 'Â§±Ëπ§' : 'ÊãæÁç≤'}ÂØµÁâ© ${pet.name} ÁöÑÊèêÈÜí`,
      data: {
        petId: pet._id.toString(),
        reminderDay: daysSinceCreated,
        isManual: true,
        suggestions: this.getReminderSuggestions(pet, daysSinceCreated)
      },
      priority: NotificationPriority.NORMAL
    });

    logger.info('ÊâãÂãïÊèêÈÜíÂ∑≤ÁôºÈÄÅ', { userId, petId });
  }

  /**
   * Áç≤ÂèñÁî®Êà∂ÁöÑÊèêÈÜíÁµ±Ë®à
   */
  static async getUserReminderStats(userId: string, startDate?: Date, endDate?: Date): Promise<{
    totalReminders: number;
    remindersByDay: Record<number, number>;
    lastReminderDate?: Date;
  }> {
    const start = startDate || new Date(Date.now() - 30 * 24 * 60 * 60 * 1000); // È†êË®≠ 30 Â§©Ââç
    const end = endDate || new Date();

    const reminders = await Notification.find({
      userId,
      type: 'REMINDER',
      createdAt: { $gte: start, $lte: end }
    }).sort({ createdAt: -1 });

    const remindersByDay: Record<number, number> = {};
    let lastReminderDate: Date | undefined;

    for (const reminder of reminders) {
      const day = reminder.data?.reminderDay as number;
      if (day !== undefined) {
        remindersByDay[day] = (remindersByDay[day] || 0) + 1;
      }
      
      if (!lastReminderDate || reminder.createdAt > lastReminderDate) {
        lastReminderDate = reminder.createdAt;
      }
    }

    return {
      totalReminders: reminders.length,
      remindersByDay,
      lastReminderDate
    };
  }

  /**
   * Áç≤ÂèñÈÖçÁΩÆ
   */
  static getConfig(): ReminderConfig {
    return { ...this.config };
  }

  /**
   * Êõ¥Êñ∞ÈÖçÁΩÆ
   */
  static updateConfig(newConfig: Partial<ReminderConfig>): ReminderConfig {
    this.config = { ...this.config, ...newConfig };
    
    // Â¶ÇÊûúÂïüÁî®ÁãÄÊÖãÊîπËÆäÔºåÈáçÊñ∞ÂïüÂãïÊàñÂÅúÊ≠¢ÊúçÂãô
    if (newConfig.enabled !== undefined) {
      this.stop();
      if (newConfig.enabled) {
        this.start();
      }
    }
    
    logger.info('ÊèêÈÜíÈÖçÁΩÆÂ∑≤Êõ¥Êñ∞', { config: this.config });
    return { ...this.config };
  }

  /**
   * Áç≤ÂèñÁµ±Ë®à‰ø°ÊÅØ
   */
  static async getStats(): Promise<{
    enabled: boolean;
    running: boolean;
    sentToday: number;
    config: ReminderConfig;
  }> {
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    const tomorrow = new Date(today.getTime() + 24 * 60 * 60 * 1000);

    const sentToday = await Notification.countDocuments({
      type: 'REMINDER',
      createdAt: {
        $gte: today,
        $lt: tomorrow
      }
    });

    return {
      enabled: this.config.enabled,
      running: this.interval !== null,
      sentToday,
      config: { ...this.config }
    };
  }
}